	import {wxLoing} from "../../utils/login.js"
	export default {
		data() {
			return {
			}
		},
		onLoad() {
		},
		methods: {
			async WxgetUserInfo(e){
				const {encryptedData,rawData,iv,signature} = e.detail
				const ivs = iv 
				if(ivs){
					const {code }= await wxLoing()
					const rawDatas = JSON.parse(rawData)
					const nick = rawDatas.nickName
					const sex = rawDatas.gender
					const avaurl = rawDatas.avatarUrl
                    const session_key = rawDatas.session_key
					uni.setStorage({
						key:"coken",
						data:{
							sex:sex,
							nick:nick,
							avatarUrl:avaurl,
							session_key:session_key
						}
					})
					const res = await this.$myRequest({
						method:"POST",
						url:"/zhuce",
						data:{
							code:code,
							nick:nick,
							sex:sex,
							avaurl:avaurl
						},
					})
					const openid = res.data
				    uni.setStorage({
				    	key:"token",
						data:openid,
						success: (res) => {
							uni.navigateBack({
								delta:1
							})
						},
						fail: () => {
						}
				    })
				}else{
					uni.showToast({
						title:"用户取消了授权"
					})
				}
			}
		}
	}
